<template>
  <d2-container>
    <template slot="header">
      <el-select
        v-model="serviceName"
        placeholder="请选择服务"
        size="small"
        @change="changeService"
        filterable
        clearable
      >
        <el-option
          v-for="item in serviceOptions"
          :key="item"
          :label="item"
          :value="item"
        >
        </el-option>
      </el-select>
      <el-select
        v-model="instanceValue"
        placeholder="请选择您的实例"
        size="small"
        @change="changeInstance"
        filterable
        clearable
        style="width: 200px"
      >
        <el-option
          v-for="(item, index) in instanceOptions"
          :key="index"
          :label="item.instanceId.split('#')[0]"
          :value="item.instanceId"
        >
        </el-option>
      </el-select>

      <div class="topTime" style="float: right">
        <strong>时间：</strong>
        <span style="padding: 3px 0">{{ serverData.crt_time }}</span>
      </div>
    </template>

    <el-card class="box-card">
      <el-form label-position="left" class="demo-table-expand" size="mini">
        <el-form-item label="PFCP会话建立请求次数："
          ><span>{{
            serverData.upf_pfcp_session_estab_req
          }}</span></el-form-item
        >
        <el-form-item label="PFCP会话建立成功次数"
          ><span>{{
            serverData.upf_pfcp_session_estab_succ
          }}</span></el-form-item
        >
        <el-form-item>
          <el-collapse accordion>
            <el-collapse-item title="PFCP会话建立失败次数" name="1">
              <el-form
                label-position="left"
                class="demo-table-expand"
                label-width="500px"
                size="mini"
              >
                <el-form-item
                  v-for="(
                    value, key, index
                  ) in serverData.pfcpSessionEstabFailCause"
                  :key="index"
                  :label="key"
                >
                  <span>{{ value }}</span>
                </el-form-item>
              </el-form>
            </el-collapse-item>
          </el-collapse>
        </el-form-item>
        <el-form-item label="PFCP会话修改请求次数"
          ><span>{{
            serverData.upf_pfcp_session_modify_req
          }}</span></el-form-item
        >
        <el-form-item label="PFCP会话修改成功次数"
          ><span>{{
            serverData.upf_pfcp_session_modify_succ
          }}</span></el-form-item
        >
        <el-form-item>
          <el-collapse accordion>
            <el-collapse-item title="PFCP会话修改失败次数" name="1">
              <el-form
                label-position="left"
                class="demo-table-expand"
                label-width="500px"
                size="mini"
              >
                <el-form-item
                  v-for="(
                    value, key, index
                  ) in serverData.pfcpSessionModifyFailCause"
                  :key="index"
                  :label="key"
                >
                  <span>{{ value }}</span>
                </el-form-item>
              </el-form>
            </el-collapse-item>
          </el-collapse>
        </el-form-item>
      </el-form>
    </el-card>
    <el-row>
      <el-select
        v-model="nsName"
        placeholder="请选择ns-name"
        size="small"
        filterable
        clearable
      >
        <el-option
          v-for="item in nsNameOptions"
          :key="item"
          :label="item"
          :value="item"
        >
        </el-option>
      </el-select>

      <el-button size="mini" type="primary" @click="changeNsName"
        >筛选</el-button
      >
    </el-row>

    <br />
    <el-tabs v-model="activeName" type="card" @tab-click="handleClick">
      <el-tab-pane
        v-for="(item, index) in tabPaneList"
        :key="index"
        :label="item.tabLabel"
        :name="item.tabLabel"
      >
        <el-table :data="serverData[item.dataKey]" style="width: 100%">
          <el-table-column prop="id" label="ID" width="180"> </el-table-column>
          <el-table-column prop="ns_name" label="ns_name" width="180">
          </el-table-column>
          <el-table-column :prop="item.columKey" label="次数">
          </el-table-column>
        </el-table>
      </el-tab-pane>
    </el-tabs>
  </d2-container>
</template>

<script>
import {
  fetchGetServiceList,
  fetchGetInstanceList,
  fetchGetPfcp,
} from "@/api/demo/index";
export default {
  name: "pfcp",
  data() {
    return {
      activeName: "分切片的PFCP会话建立请求次数",
      serviceName: "",
      serviceOptions: [],
      instanceOptions: [], //upf下面的实例
      instanceValue: "", //当前选中的实例
      nsName: "",
      nsNameOptions: [],
      isFirst: true,
      loading: false,
      serverData: {},
      oldServerData: {},
      tabPaneList: [
        {
          tabLabel: "分切片的PFCP会话建立请求次数",
          dataKey: "pfcpSessionEstabReqNsList",
          columKey: "pfcp_session_estab_req",
        },
        {
          tabLabel: "分切片的PFCP会话建立成功次数",
          dataKey: "pfcpSessionEstabSuccNsList",
          columKey: "pfcp_session_estab_succ",
        },
        {
          tabLabel: "分切片的PFCP会话修改请求次数",
          dataKey: "pfcpSessionModifyReqNsList",
          columKey: "pfcp_session_modify_req",
        },
        {
          tabLabel: "分切片的PFCP会话修改成功次数",
          dataKey: "pfcpSessionModifySuccNsList",
          columKey: "pfcp_session_modify_succ",
        },
        {
          tabLabel: "分切片的PFCP会话建立失败次数",
          dataKey: "pfcpSessionEstabFailCauseNsList",
          columKey: "pfcp_session_estab_fail_cause",
        },
        {
          tabLabel: "分切片的PFCP会话修改失败次数",
          dataKey: "pfcpSessionModifyFailCauseNsList",
          columKey: "pfcp_session_modify_fail_cause",
        },
      ],
    };
  },
  mounted() {
    this.getService();
  },
  methods: {
    handleClick(tab, event) {
      console.log(tab, event);
    },
    // 获取服务列表
    getService() {
      fetchGetServiceList({
        pageNo: 1,
        pageSize: 50,
      })
        .then((response) => {
          console.log(response);
          this.serviceOptions = response.doms.filter(
            (item) => !item.startsWith("ace")
          );
          if (this.serviceOptions.length > 0) {
            this.serviceName = this.serviceOptions[0];
            this.getServiceInstance(this.serviceName);
          }
        })
        .catch((e) => {
          console.log(e);
          this.$alert(e, "网络请求发生错误", {
            confirmButtonText: "确定",
            callback: (action) => {},
          });
        });
    },
    // 选择服务
    changeService(val) {
      if (!val) {
        return;
      }
      this.getServiceInstance(val);
    },
    // 选择实例
    changeInstance(val) {
      // 图表置空
      this.serverData = [];

      // 获取告警数据
      this.getPfcp();
    },
    //获取服务下面的实例
    getServiceInstance(serviceName) {
      fetchGetInstanceList({ serviceName: serviceName })
        .then((res) => {
          this.instanceOptions = res.hosts;
          if (this.instanceOptions.length > 0) {
            this.instanceValue = this.instanceOptions[0].instanceId;
            this.changeInstance(this.instanceValue);
          }
        })
        .catch((e) => {
          console.log(e);
          this.$alert(e, "网络请求发生错误", {
            confirmButtonText: "确定",
            callback: (action) => {},
          });
        });
    },
    changeNsName() {
      if (this.nsName) {
        this.serverData.pfcpSessionEstabFailCauseNsList.filter(
          (item) => item.ns_name === this.nsName
        );
        this.serverData.pfcpSessionEstabReqNsList.filter(
          (item) => item.ns_name === this.nsName
        );
        this.serverData.pfcpSessionEstabSuccNsList.filter(
          (item) => item.ns_name === this.nsName
        );
        this.serverData.pfcpSessionModifySuccNsList.filter(
          (item) => item.ns_name === this.nsName
        );
        this.serverData.pfcpSessionModifyReqNsList.filter(
          (item) => item.ns_name === this.nsName
        );
        this.serverData.pfcpSessionModifySuccNsList.filter(
          (item) => item.ns_name === this.nsName
        );
      } else {
        this.serverData = this.oldServerData;
      }
    },
    // 获取数据
    getPfcp() {
      this.loading = true;
      fetchGetPfcp({
        request_type: "pfcp perform",
        instance_id: this.instanceValue,
        sequence_no: 3353,
      })
        .then((response) => {
          this.loading = false;
          if ((response != null) & (response != [])) {
            this.oldServerData = response;
            this.serverData = response;
            // 后台不处理数据 前端处理数据
            let arr1 = this.serverData.pfcpSessionEstabFailCauseNsList.map(
              (item) => item.ns_name
            );
            let arr2 = this.serverData.pfcpSessionEstabReqNsList.map(
              (item) => item.ns_name
            );
            let arr3 = this.serverData.pfcpSessionEstabSuccNsList.map(
              (item) => item.ns_name
            );
            let arr4 = this.serverData.pfcpSessionModifySuccNsList.map(
              (item) => item.ns_name
            );
            let arr5 = this.serverData.pfcpSessionModifyReqNsList.map(
              (item) => item.ns_name
            );
            let arr6 = this.serverData.pfcpSessionModifySuccNsList.map(
              (item) => item.ns_name
            );
            this.nsNameOptions = [
              ...new Set([
                ...arr1,
                ...arr2,
                ...arr3,
                ...arr4,
                ...arr5,
                ...arr6,
              ]),
            ];
          }
        })
        .catch((e) => {
          this.loading = false;
          console.log(e);
          this.$alert(e, "网络请求发生错误", {
            confirmButtonText: "确定",
            callback: (action) => {},
          });
        });
    },
  },
};
</script>


